
body {
	display: flex;
	justify-content: center;
	justify-items: baseline;
	font-family: 'Montserrat', sans-serif;	
    background-image: linear-gradient(to right, #fce3ec, rgb(255, 232, 204));
}

:root {
	margin-top: 5rem;
	
}

.SyApp {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	background-color: white;
	border-radius: 5px;
	box-shadow: 8px 9px 7px 0px #ccc;
}

.SyApp .flexContainer {
	display: flex;
	justify-content: center;
	min-width: 280px;
	max-width: 300px;
	padding: 20px;
}
.SyApp .searchBox {
	background-color: #f5f5f5;
	border-radius: 5px 0 0 5px;
}
.SyApp .synonyms {
	background-color: #e8554e;
	border-radius: 0 5px 5px 0;
}

.SyApp .synonyms .wordTagContainer, 
.SyApp .searchBox .searchContainer {
	width: 320px;
	padding: 20px 0;
}

.SyApp .synonyms .wordTag{
	float: left;
	background-color: #FF4B2B;
	color: white;
	margin: 0.1rem 0.2rem;
	padding: 0.3rem;
	border-radius: 5px;
}

.SyApp button {
	border-radius: 1rem;
	border: 1px solid #FF4B2B;
	color: #FFFFFF;
	font-weight: bold;
	padding: 12px 45px;
}

.SyApp input {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
}

.SyApp .submitButton:hover {
	background-color: #ccc;
	border: none;
	
}

.SyApp form {
	width: 100%;
	margin: 10px 0;
}

@media only screen and (max-width: 680px){
	.SyApp .flexContainer {
		width: 100%;
		max-width: 100%;
	}
	.SyApp .synonyms {
		border-radius: 0;
	}
}

.clearfix::after {
	content: "";
	clear: both;
	display: table;
  }